:root {
  --animate-duration: 280ms;
}
html,
body,
#root {
  height: 100%;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.flex {
  display: flex;
}
.animate__fadeInRightSmall {
  animation-name: fadeInRightSmall;
  animation-duration: var(--animate-duration);
  animation-iteration-count: var(--animate-repeat);
  animation-fill-mode: both;
}
@keyframes fadeInRightSmall {
  0% {
    opacity: 0;
    transform: translate3d(60px, 0, 0);
  }
  to {
    opacity: 1;
    transform: translateZ(0);
  }
}
.animate__fadeInUpSmall {
  animation-name: fadeInUpSmall;
  animation-duration: var(--animate-duration);
  animation-iteration-count: var(--animate-repeat);
  animation-fill-mode: both;
}
@keyframes fadeInUpSmall {
  0% {
    opacity: 0;
    transform: translate3d(0, 60px, 0);
  }
  to {
    opacity: 1;
    transform: translateZ(0);
  }
}
.animate__zoomIn {
  animation-name: zoomIn;
  animation-duration: var(--animate-duration);
  animation-iteration-count: var(--animate-repeat);
  animation-fill-mode: both;
}
@keyframes zoomIn {
  0% {
    width: 300px;
    height: 280px;
  }
  to {
    width: 552px;
    height: 588px;
  }
}
.animate__zoomInImg {
  animation-name: zoomInImg;
  animation-duration: var(--animate-duration);
  animation-iteration-count: var(--animate-repeat);
  animation-fill-mode: both;
}
@keyframes zoomInImg {
  0% {
    bottom: 100px;
  }
  to {
    bottom: 0px;
  }
}
.animate__rightImg {
  animation-name: rightImg;
  animation-duration: var(--animate-duration);
  animation-iteration-count: var(--animate-repeat);
  animation-fill-mode: both;
}
@keyframes rightImg {
  0% {
    left: 0;
    width: 100%;
  }
  to {
    left: 550px;
    width: 600;
  }
}
.animate__leftImg {
  animation-name: leftImg;
  animation-duration: var(--animate-duration);
  animation-iteration-count: var(--animate-repeat);
  animation-fill-mode: both;
}
@keyframes leftImg {
  0% {
    right: 0;
    width: 100%;
  }
  to {
    right: 550px;
    width: 600;
  }
}
